<template>
  <div class="index">

    <!-- banner -->
    <div class="jumbotron">
      <div class="container">
        <div class="text-center">
          <h1>Vue-Video-Player</h1>
          <h3>Video Player component for Vue.js</h3>
          <br>
          <p>
            <a href="https://github.com/surmon-china/vue-video-player" class="btn btn-primary btn-lg" target="_blank" v-touch-ripple>View on GitHub</a>
            <span>&nbsp;</span>
            <a class="btn btn-success btn-lg" @click="playerAction('play')" v-touch-ripple>play all video</a>
            <span>&nbsp;</span>
            <a class="btn btn-warning btn-lg" @click="playerAction('pause')" v-touch-ripple>pause all video</a>
            <span>&nbsp;</span>
            <a class="btn btn-info btn-lg" @click="playerAction('refresh')" v-touch-ripple>refresh all video</a>
          </p>
        </div>
      </div>
    </div>

    <!-- editor lists -->
    <div class="container">

      <!-- data bind -->
      <div class="row">
        <div class="col-xs-12">
          <div class="panel panel-default">
            <div class="panel-heading" v-touch-ripple>
              <h4 class="text-center">
                <span>Use and code example</span>
              </h4>
            </div>
            <div class="panel-body">
              <div class="row">
                <div class="col-xs-6">
                  <video-player :options="videoOption1"></video-player>
                </div>
                <div class="col-xs-6">
                  <codemirror :code.sync="exampleEditorCode1" :options="exampleEditorOption" class="exampleEditor"></codemirror>
                </div>
              </div>
              <br>
              <div class="row">
                <div class="col-xs-6">
                  <video-player :options="videoOption2"></video-player>
                </div>
                <div class="col-xs-6">
                  <codemirror :code.sync="exampleEditorCode2" :options="exampleEditorOption" class="exampleEditor"></codemirror>
                </div>
              </div>
              <br>
              <div class="row">
                <div class="col-xs-6">
                  <video-player :options="videoOption3"></video-player>
                </div>
                <div class="col-xs-6">
                  <codemirror :code.sync="exampleEditorCode3" :options="exampleEditorOption" class="exampleEditor"></codemirror>
                </div>
              </div>
              <br>
              <div class="row">
                <div class="col-xs-6">
                  <video-player :options="videoOption4"></video-player>
                </div>
                <div class="col-xs-6">
                  <codemirror :code.sync="exampleEditorCode4" :options="exampleEditorOption" class="exampleEditor"></codemirror>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  const exampleEditorOption = {
    tabSize: 2,
    styleActiveLine: true,
    lineNumbers: true,
    line: true,
    mode: 'text/javascript',
    lineWrapping: false,
    theme: 'base16-dark'
  }

  let exampleEditorCode1 =  
  `export default {
    data () {
      return {
        videoOptions: {
          source: [
            { type: "video/mp4", src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_H', label: '原画', res: 1 },
            { type: "video/mp4", src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_M', label: '高清', res: 2 },
            { type: "video/mp4", src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_L', label: '流畅', res: 3 }
          ],
          language: 'zh-CN',
          playbackRates: [0.7, 1.0, 1.3, 1.5, 1.7],
          poster: 'http://cn.vuejs.org/images/logo.png',
          defaultSrcReId: 2,
          height: 380
        }
      }
    }
  }`

  let exampleEditorCode2 =  
  `export default {
    data () {
      return {
        videoOptions: {
          source: {
            type: "video/mp4",
            src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_2_M'
          },
          playbackRates: [0.5, 1.0, 1.5, 2.0],
          muted: true
        }
      }
    }
  }`

  let exampleEditorCode3 =  
  `export default {
    data () {
      return {
        videoOptions: {
         source: {
          type: "video/webm",
          src: 'http://techslides.com/demos/sample-videos/small.webm',
        },
        poster: 'http://www.freemake.com/blog/wp-content/uploads/2015/06/videojs-logo.jpg',
        autoplay: false
      }
    }
  }`

  let exampleEditorCode4 =  
  `export default {
    data () {
      return {
        videoOptions: {
          source: 'http://7xnbft.com2.z0.glb.clouddn.com/sample_live_video.hls',
          live: true
        }
      }
    }
  }`

  const videoOption1 = {
    source: [
      { type: "video/mp4", src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_H', label: '原画', res: 1 },
      { type: "video/mp4", src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_M', label: '高清', res: 2 },
      { type: "video/mp4", src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_L', label: '流畅', res: 3 }
    ],
    language: 'zh-CN',
    playbackRates: [0.7, 1.0, 1.3, 1.5, 1.7],
    poster: 'http://cn.vuejs.org/images/logo.png',
    defaultSrcReId: 2,
    height: 380
  }

  const videoOption2 = {
    source: {
      type: "video/mp4",
      src: 'http://7xkwa7.media1.z0.glb.clouddn.com/sample_video_2_M'
    },
    playbackRates: [0.5, 1.0, 1.5, 2.0],
    muted: true
  }

  const videoOption3 = {
    source: {
      type: "video/webm",
      src: 'http://techslides.com/demos/sample-videos/small.webm',
    },
    poster: 'http://www.freemake.com/blog/wp-content/uploads/2015/06/videojs-logo.jpg',
    autoplay: false
  }

  const videoOption4 = {
    source: 'http://7xnbft.com2.z0.glb.clouddn.com/sample_live_video.hls',
    // poster: 'http://n.sinaimg.cn/gx/crawl/20160908/2vEt-fxvqqsk5006740.jpg',
    live: true,
    // autoplay: false
  }

  export default {
    data () {
      return {
        exampleEditorOption,
        videoOption1,
        exampleEditorCode1,
        videoOption2,
        exampleEditorCode2,
        videoOption3,
        exampleEditorCode3,
        videoOption4,
        exampleEditorCode4
      }
    },
    // 在创建实例时 `events` 选项简单地调用 `$on`
    events: {
      'player': function (msg) {
        // console.log(msg)
      }
    },
    methods: {
      playerAction: function (action) {
        this.$broadcast('player', action)
      }
    }
  }
</script>

<style lang="scss">
  .CodeMirror.cm-s-base16-dark {
    height: 380px!important;
  }
</style>


